<template>
    <div class="curinfo">
        <ul>
            <li v-for="item in curinfoData">
                {{item.title}}: {{item.value}}
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:'curinfo',
    data(){
        return{
            curinfoData:[]
        }
    },
    mounted(){
        this.curinfoData = this.getCurInfo();
    },
    methods:{
        getCurInfo(){
            var curInfoDatas;
            if(this.isApp){
                curInfoDatas = [
                    {title:'App ID',value:api.appId},
                    {title:'App名字',value:api.appName},
                    {title:'应用版本号',value:api.appVersion},
                    {title:'引擎版本信息',value:api.version},
                    {title:'系统类型',value:api.systemType},
                    {title:'系统版本',value:api.systemVersion},
                    {title:'设备标识',value:api.deviceId},
                    {title:'设备型号',value:api.deviceModel},
                    {title:'设备名称',value:api.deviceName},
                    {title:'运营商名称',value:api.operator},
                    {title:'当前网络连接类型',value:api.connectionType},
                    {title:'屏幕分辨率',value:'宽：'+api.screenWidth+' 高：'+api.screenHeight},
                    {title:'主窗口名字',value:api.winName},
                    {title:'主窗口宽度',value:api.winWidth},
                    {title:'主窗口高度',value:api.winHeight},
                    {title:'子窗口名字',value:api.frameName},
                    {title:'fsDir目录',value:api.fsDir},
                    {title:'cacheDir目录',value:api.cacheDir},
                    {title:'boxDir目录',value:api.boxDir},
                ]
            }
            return curInfoDatas
        }
    }
}
</script>
<style>
    .curinfo{
        padding: 20px;
    }
    .curinfo li{
        padding: 10px 0;
        font-size: 16px;
    }
</style>


